import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function enterpriseValueMultipleTtm(tableName: string) {
  const title = "EV/EBIT (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["enterpriseValueMultipleTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "EV vs. EBIT.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about EV/EBIT or other valuation measures?`,
              value:
                info.row.original.financialData.companyRatios?.data?.enterpriseValueMultipleTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.enterpriseValueMultipleTtm}
        />
      )
    },
  })
}

export { enterpriseValueMultipleTtm }
